	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Rent A German","date":"23 Apr 2005","slug":"/2005-04-23-rent-a-german/","tags":"Y2005"},"html":"<p>I don't really need to <a href=\"http://www.rentagerman.de/\" title=\"rent a german\">Rent A German</a>. I've already got one on long-term lease.</p>"},"next":{"fields":{"title":"Paris Later","date":"23 Apr 2005","slug":"/2005-04-23-paris-later/"}},"previous":{"fields":{"title":"Music Charts","date":"25 Apr 2005","slug":"/2005-04-25-music-charts/"}}},{"node":{"fields":{"title":"Paris Later","date":"23 Apr 2005","slug":"/2005-04-23-paris-later/","tags":"Y2005"},"html":"<p>Since Christopher will be on the North Sea for his <a href=\"http://www.papascott.de/archives/2005/04/06/practice-for-freetime/\" title=\"PapaScott: Practice for Freetime\">Kindergarten Freetime</a> this week, I could very well have arranged to attend the <a href=\"http://www.lesblogs.typepad.com/\">Les Blogs</a> (that's Les as in French, not Les as in lesbian) conference in Paris on Monday. But since we've never been to Paris, it would be somehow unfair to go to Paris without my wife, wouldn't it? (Although the first vision I have of going to Paris with my wife is losing her like in <a href=\"http://imdb.com/title/tt0095174\">Frantic</a>... would I go jumping on roofs like Harrison Ford to look for her? Let's hope I never have to find out!)</p>\n<p>Anyway, I suspect our first trip to Paris will not be a romantic second honeymoon, but rather a visit with Christopher to see the <a href=\"http://www.disneylandparis.com/\">mouse</a> at Disneyland Paris.</p>"},"next":{"fields":{"title":"Methamphetamine wreckage in Montana","date":"23 Apr 2005","slug":"/2005-04-23-methamphetamine-wreckage-in-montana/"}},"previous":{"fields":{"title":"Rent A German","date":"23 Apr 2005","slug":"/2005-04-23-rent-a-german/"}}},{"node":{"fields":{"title":"Methamphetamine wreckage in Montana","date":"23 Apr 2005","slug":"/2005-04-23-methamphetamine-wreckage-in-montana/","tags":"Y2005"},"html":"<p>New West has a <a href=\"http://www.newwest.net/index.php/main/article/546\" title=\"New West Network | Part 1: Inside the World of the 'Dasen Girls'\">bizarre 6 part story of prostitution and methamphetamines in rural Montana</a>. I've heard of meth labs near my old home town in Minnesota, but meth is a rural phenomenon that began after I left, so the disruption and destruction that meth addiction can cause in small towns was new to me, especially when the economy is failing. In this particular story a local businessman is taking advantage of young women who need money for the drug. After a woman is killed in a car accident, her mother had the courage to gather evidence and go to the police. <a href=\"http://www.markarkleiman.com/archives/_/2005/04/methamphetamine_wreckage_in_montana.php\" title=\"Mark A. R. Kleiman: Methamphetamine wreckage in Montana\">(via Mark A. R. Kleiman)</a></p>"},"next":{"fields":{"title":"airport penguins","date":"23 Apr 2005","slug":"/2005-04-23-airport-penguins/"}},"previous":{"fields":{"title":"Paris Later","date":"23 Apr 2005","slug":"/2005-04-23-paris-later/"}}}],"pathPrefix":"","first":false,"last":false,"index":320,"pageCount":1101}}

